.carousel-fade {
  .carousel-item {
    display: block;
    position: absolute;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
    z-index: 0;

    &.active {
      opacity: 1;
    }

    img {
      position: fixed;
      // padding-bottom:0;
      background: $dark-color;
      background-attachment: fixed;
      background-size: cover;
      background-position: center;
      height: 100%;
      width: 100vw;
    }
  }
}

// Custom styles

html,
body,
.carousel,
.carousel-inner,
.carousel-item {
  width: 100vw;
  height: 100vh;
  // transition: $carousel-transition;
}

.carousel {
  // position: absolute;
  top: 85px;
  left: 0;
  z-index: 0;
  position: fixed;
  // padding-bottom:0;
  background: $dark-color;
  // background-attachment: fixed;
  // background-size: cover;

  .carousel-indicators li {
    width: 15px;
    height: 15px;
    border-radius: 100%;
  }
}

/* how long VUE VUE VUE VUE transition will take */
.slide-fade-enter-active {
  transition: 1s ease;
}
.slide-fade-leave-active {
  transition: 1s ease;
}

// STYLINGS FOR THE INPUT FORM ON THE CONTACT PAGE //

.section-contact {
  input,
  label {
    display: block;
  }

  .container {
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
    padding: 1em;
  }

  ul {
    list-style: none;
    padding: 0;
    font-size: 30px;
  }

  .brand {
    text-align: center !important;
  }

  .wrapper {
    // box-shadow: 0 0 20px 0 rgba(72, 94, 116, 0.7);
  }

  .wrapper > * {
    padding: 1em;
  }

  .company-info h3,
  .company-info ul {
    text-align: center;
    margin: 0 0 1rem 0;
  }

  .form-img {
    height: 300px;
    margin: 15px auto;
    width: 100%;
    display: block;
  }

  .contact {
    background: $primary-color;
  }

  // FORM STYLES - INNPUTS AND WHATNOT //

  .contact {
    form {
      display: grid;
      grid-gap: 20px;

      button {
        background: #ebffff;
        border: 0;
        text-transform: uppercase;
      }

      label {
        display: block;
        font-size: 1.5rem;
      }
      ul {
        p {
          margin: 0;
          font-size: 30px;
        }
      }

      li {
        line-height: 3;
        img {
          height: 100px;
          width: 100px;
        }
      }

      .full {
        grid-column: 1 / 3;
      }

      button,
      input,
      textarea {
        width: 100%;
        padding: 0.25rem;
        font-size: 30px;
        border: 1px solid $bg;
        transition: all 1s ease-out;
      }
    }

    .contact-radio {
      height: 15px;
      width: 15px;
    }
  }

  .contact form button:hover,
  .contact form button:focus {
    background: #0070cd;
    color: #fff;
    outline: 0;
  }
}
